
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Calendar from 'vux/src/components/calendar/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name: 'addCoupons',
components: {
XButton,
XInput,
Group,
Calendar
},
data () {
return {
startTime: '',
endTime: '',
params: {
amount: '',
bizType: '',
name: '',
type: '',
lowerLimitAmount: '',
buildQuantity: '',
startTime: '',
endTime: ''
}
}
},
methods: {
check (val,type) {
if(!val || val < 0) {
this.params[type] = 0
}
},
changeType1(status) {
this.params.type = status;
},
changeType2(status){
this.params.bizType = status
},
getBack () {
this.$router.go(-1)
},
onChangeStart (val) {
console.log('change', val)
this.params.startTime = val
},
onChangeEnd (val) {
console.log('change', val)
this.params.endTime = val
},
postData () {
if(!this.params.type) {
this.$vux.toast.show({
text: '请选择优惠券类型',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.bizType) {
this.$vux.toast.show({
text: '请选择业务类型',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.name) {
this.$vux.toast.show({
text: '请填写优惠券名称',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.amount) {
this.$vux.toast.show({
text: '请输入优惠金额',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.lowerLimitAmount) {
this.$vux.toast.show({
text: '请输入优惠门槛',
position: 'middle',
type: 'warn'
})
return
}
if(Number(this.params.amount) > Number(this.params.lowerLimitAmount) || Number(this.params.amount) == Number(this.params.lowerLimitAmount)){
this.$vux.toast.show({
text: '优惠门槛不能小于优惠金额',
position: 'middle',
type: 'warn'
})
return
}
if(!this.params.buildQuantity) {
this.$vux.toast.show({
text: '请输入发行张数',
position: 'middle',
type: 'warn'
})
return
}
console.log(this.params.startTime)
if(!this.startTime) {
this.$vux.toast.show({
text: '请选择开始日期',
position: 'middle',
type: 'warn'
})
return
}
if(!this.endTime) {
this.$vux.toast.show({
text: '请选择结束日期',
position: 'middle',
type: 'warn'
})
return
}
let aa = this.startTime
this.params.startTime = aa.replace(/-/g,'.')
let bb = this.endTime
this.params.endTime = bb.replace(/-/g,'.')
this.$vux.loading.show({
text: '加载中...'
})
this.params['merchantId'] = this.$store.state.merchantId
this.$http.fetchPost('/merchant/post/add/coupon', this.params).then((res) => {
this.$vux.loading.hide()
if (res.data.code === 200) {
this.$vux.toast.show({
text: '操作成功',
position: 'middle'
})
this.$router.go(-1)
} else {
this.$vux.toast.show({
text: res.data.message,
position: 'middle',
type: 'warn'
})
}
})
}
}
}



